<template>
  <el-container v-loading="loading">
    <div class="box">
      <h3>{{activeheader}}</h3>
      <div class="input-box">
        <label for="" class="label require"><i>姓名：</i></label>
        <el-input class="input-width" v-model="name" placeholder="请输入用户姓名"></el-input>
      </div>
      <div class="input-box">
        <label for="" class="label require"><i>姓别：</i></label>
        <el-select v-model="sex" placeholder="全部">
          <el-option
            v-for="item in sexList"
            :key="item.dValue"
            :label="item.dValue"
            :value="item.dKey">
          </el-option>
        </el-select>
      </div>
      <div class="input-box">
          <label class="label require"><i>地区：</i></label>
          <user-address :defaultValue="defaultAddress" ref="hosAddAddress" seletStyle="width:217px"/>
      </div>
      <div class="input-box">
          <label class="label require"><i>所在医院：</i></label>
          <el-select v-model="type" placeholder="请选择">
            <el-option
              v-for="item in sexList"
              :key="item.id"
              :label="item.dValue"
              :value="item.dKey">
            </el-option>
          </el-select>
      </div>
      <div class="input-box">
        <label for="" class="label require"><i>身份证号：</i></label>
        <el-input class="input-width" v-model="phone" placeholder="请输入内容"></el-input>
      </div>
      <div class="input-box">
        <label for="" class="label require"><i>手机号码：</i></label>
        <el-input class="input-width" v-model="phone" placeholder="请输入内容"></el-input>
      </div>
      <div class="input-box">
        <label for="" class="label require"><i>银行名称：</i></label>
        <el-input class="input-width" v-model="phone" placeholder="请输入内容"></el-input>
      </div>
      <div class="input-box">
        <label for="" class="label require"><i>银行账号：</i></label>
        <el-input class="input-width" v-model="phone" placeholder="请输入内容"></el-input>
      </div>
      <div class="input-box">
        <label for="" class="label"></label>
        <el-radio v-model="phone" label="1">地区负责人</el-radio>
      </div>
      <div class="input-box">
        <label class="label require"><i>人员技能标签：</i></label>
        <el-select v-model="type" placeholder="请选择">
          <el-option
            v-for="item in sexList"
            :key="item.id"
            :label="item.dValue"
            :value="item.dKey">
          </el-option>
        </el-select>
      </div>
      <div class="input-box">
        <label class="label"></label>
        <div class="input-tip">
          <span>押金垫付</span>
          <span>押金垫付</span>
        </div>    
      </div>

      <div class="input-box box-last">
        <el-button type="primary" @click="submit">提交</el-button>
        <el-button @click="cancle">取消</el-button>
      </div>
    </div>
  </el-container>
</template>

<script>
import check from "utils/check";
import { sexList } from "data/listBody";
import UserAddress from "components/address";
let headers = ["新增人员", "修改信息"];
let requireArray = [
  "name",
  "type",
  "level",
  "province",
  "city",
  "country",
  "shebao"
]; //必传字段

export default {
  name: "addUserfiled",
  data() {
    return {
      name: "",
      otherName: "",
      type: "",
      level: "",
      phone: "",
      address: "",
      remark: "",
      info: "",
      id: "",
      sex: "",
      loading: false,
      shebao: false,
      defaultAddress: null, //默认地址
      activeheader: headers[0],
      check: check.methods.check,
      checkTip: check.tips.checkTip,
      sexList
    };
  },
  components: {
    UserAddress
  },
  mounted() {
    // this.editInit();
  },
  methods: {
    editInit() {
      let { type, id } = this.$route.query;
      if (type === "edit") this.activeheader = headers[1];
      if (!id) return;
      this.loading = true;
      this.api
        .get(`/hospital/${id}`)
        .then(res => {
          //查询产品详情
          this.dataInit(res.data);
          setTimeout(() => {
            this.loading = false;
          }, 20);
        })
        .catch(err => {
          console.log(err);
          this.loading = false;
          this.$message(err.data.message);
        });
    },
    dataInit(data) {
      data = data || {};
      let {
        name,
        otherName,
        type,
        level,
        phone,
        address,
        remark,
        info,
        id,
        province,
        city,
        country,
        shebao
      } = data;
      this.name = name;
      this.otherName = otherName;
      this.type = type;
      this.level = level;
      this.phone = phone;
      this.address = address;
      this.remark = remark;
      this.info = info;
      this.id = id;
      this.shebao = shebao;
      this.defaultAddress = {
        province,
        city,
        country
      };
    },
    submit() {
      let addressParams = this.$refs.hosAddAddress.getData();
      let params = {
        name: this.name,
        otherName: this.otherName,
        type: this.type,
        level: this.level,
        phone: this.phone,
        address: this.address,
        info: this.info,
        remark: this.remark,
        shebao: this.shebao
      };
      let param = this.lodash.assign({}, params, addressParams);
      this.check(param, requireArray)
        .then(
          () => {
            this.addUser(param);
          },
          () => {
            this.$message(this.checkTip);
          }
        )
        .catch(err => {
          console.log(err);
          this.$message(err.data.message);
        });
    },
    editUser(param) {
      //修改用户
      param.id = this.id;
      this.loading = true;
      this.api
        .put("/hospital", param, "form")
        .then(res => {
          this.loading = false;
          this.$router.go(-1);
        })
        .catch(err => {
          console.log(err);
          this.loading = false;
          this.$message(err.data.message);
        });
    },
    addUser(param) {
      //添加用户
      if (this.$route.query.type === "edit") return this.editUser(param);
      this.loading = true;
      this.api
        .post("/hospital", param, "form")
        .then(res => {
          this.$router.go(-1);
          this.loading = false;
        })
        .catch(err => {
          console.log(err);
          this.loading = false;
          this.$message(err.data.message);
        });
    },
    cancle() {
      this.$router.go(-1);
    }
  }
};
</script>
 
<style lang="less" scoped>
.box {
  padding-left: 10px;
  h3 {
    padding-left: 40px;
  }
}
.input-box {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  &.box-last {
    margin-top: 30px;
    margin-left: 60px;
  }
}
.label {
  width: 120px;
  min-width: 120px;
  margin-left: 20px;
  text-align: right;
  display: inline-block;
  font-style: normal;
  &.require {
    i {
      position: relative;
      font-style: normal;
      &::after {
        position: absolute;
        content: "*";
        color: red;
        left: -10px;
        top: 2px;
      }
    }
  }
}
.label-left {
  margin-left: 15px;
}
.input-width {
  width: 216px;
}
.el-textarea {
  width: 300px;
}
.el-select {
  margin-right: 10px;
}
</style>
 